<template>
  <div class="reply_detail">
    <cube-scroll :data="replys" :options="replyScrollOptions" @pulling-up="replyPullingUp()">

      <event-comment :comments="comment"></event-comment>

      <div>相关回复 共23条</div>

      <div class="reply" v-for=" item in replys">
        <div class="reply_info">
          <div class="left">
            <img src="../../assets/touxiang.png">
            <div class="user_info" style="margin-top: 0.17rem;margin-left: 0.24rem">
              <div>包子大叔</div>
              <div style="font-size: 0.20rem;margin-top: 0.08rem">1天前</div>
            </div>
          </div>
          <div style="font-size: 0.2rem;">
            <img class="zan_comment" src="../../assets/dianzan.png"><label> 999</label>
          </div>
        </div>
        <div class="content_style reply_content">Dota2 音乐会有参加的吗？一起的下方留言！地点：北京凯迪拉克中心网易2001年正式成立在线游戏事业部，经过近20年的快速发展，网易的品牌价值已超过13亿美元，并跻身全球七大游戏公司之一</div>
        <div class="reply_spec bottom-line"></div>
      </div>
    </cube-scroll>
  </div>
</template>

<script type="text/javascript">

  import EventComment from 'pages/event-comment.vue'
  import { scrollOptions } from 'common/js/util.js'

  export default {
    data () {
      return {
        isFollow: false,
        tabLabels: [{
          label: '详情'
        }, {
          label: '游戏'
        }, {
          label: '动态'
        }],
        replyScrollOptions: scrollOptions(false, true),
        comment: ['1'],
        replys: ['1', '2', '3', '4'],
        abstract: '网易2001年正式成立在线游戏事业部，经过近20年的快速发展，网易的品牌价值已超过13亿美元，并跻身全球七大游戏公司之一。'
      }
    },
    methods: {
      goBack() {
        this.$router.back()
      },
      replyPullingUp() {
        setTimeout( () => {
          this.replys = this.replys.concat(['1','2'])
        }, 1500)
      }

    },
    computed: {

    },
    components: {
      EventComment
    },
    mounted() {
      // window.scrollTo(0, 0)
    },
    destroyed() {
      console.log(" reply_detail destroyed")
    }
  }
</script>


<style scoped lang="stylus">
  @import "~common/stylus/variable"

  .reply_detail
    position: absolute
    top: 0
    left: 0
    rifht: 0
    bottom: 0
    padding: 0 0.19rem
    color: $content-color
    font-size: 0.25rem
    background-color: #FFF
    .reply
      position: relative
      padding: 0.34rem 0.42rem 0 0.34rem
      .reply_info
        display: flex
        justify-content: space-between
        align-items: center
        .left
          display: flex
          img
            width: 0.72rem
            height: 0.72rem
      .reply_content
        padding-left: 0.92rem
        padding-right: 0.79rem
        padding-top: 0.23rem
      .reply_spec
         margin-top: 0.54rem
    
  
</style>


