<template>
  <div>
    <div class="event_comment_wrap" v-for="item in comments">
      <img class="avatar" src="../assets/touxiang.png">
      <div class="info">
        <!-- 当前评论的用户相关 -->
        <div class="user">
          <div class="use_name" style="margin-top: 0.17rem;">
            <div>用户名称几个字</div>
            <div style="font-size: 0.20rem;margin-top: 0.08rem">2小时前</div>
          </div>
          <label class="user_follow">已关注</label>
        </div>
        <!-- 当前评论的评论内容 -->
        <div class="comment three_line_text">做一个会生活的人,做一个会生活的人,做一个会生活的人,做一个会生活的人,做一个会生活的人,做一个会生活的人,做一个会生活的人,做一个会生活的人,做一个会生活的人</div>
        <!-- 当前评论的评论总数和赞总数 -->
        <div class="total-reply-zan">
          <img class="zan_comment" src="../assets/huifu.png"><label> 999 </label>
          <img class="zan_comment" src="../assets/dianzan.png"><label> 999</label>
        </div>
        <!-- 当前评论的回复 -->
        <div class="other_reply_wrap" v-if="commentReplys.length > 0" @click="goToReplyDetail(item)">
          <div class="other_reply" v-for="reply in commentReplys">
            <label class="other_name">梦幻的悟空bbbbb</label>
            <label class="other_content content_style">：有的人活着，但是他已经死了,有的人死了，但是他还活着</label>
          </div>
          <div class="more_other_replay">查看全部25条评论>></div>
        </div>

      </div>
    </div>
  </div>
</template>

<script >
  import SendMessageToApp from '../jsAppInteractive'

  export default {
    props: {
      comments: {
        type: Array,
        default: function () {
          return ['1','2','3','2']
        }
      },
      commentReplys: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    data() {
      return {}
    },
    methods: {
      goToReplyDetail(item) {
        console.log("sssss")
        SendMessageToApp('goToReplyDetail')
        // this.$router.push({
        //   name: 'ReplyDetail'
        // })
      },
      resolveQuestionFollowers(item) {
        return `${item.answers} 赞同 · ${item.followers} 评论`
      }
    }
  }
</script>

<style scoped lang="stylus">

  .event_comment_wrap
    display: flex
    margin-bottom: 0.18rem
    padding: 0.17rem 0.20rem 0.20rem 0.14rem
    border: 1px solid rgb(225, 225, 225)
    border-radius: 3px
    box-sizing: content-box
    .info
      display: flex
      width: calc(100% - 0.72rem - 0.34rem - 0.24rem) //100% - 头像 - 边距
      flex-direction: column
      margin-left: 0.24rem
      .user
        display: flex
        justify-content: space-between
        align-items: center
      .comment
        margin-top: 0.23rem
      .total-reply-zan
        margin-top: 0.25rem
        font-size: 0.20rem
      .other_reply_wrap
        // max-height: 2.52rem
        margin-top: 0.24rem
        padding: 0.22rem 0.23rem 0.19rem 0.22rem
        border-radius: 5px
        background-color: #FCF6F6
        display: flex
        flex-direction: column
        .other_reply
          margin-bottom: 0.18rem
          .other_name
            color: #FFA535
          .other_content
            color: #000000
        .more_other_replay
          align-self: flex-end
          
          
</style>


